<template>
    <div class="loginwrap">
        <div class="login">
            <h1>{{msg}}</h1>
            <div class="account">
                            账号
                <span></span>
                <input type="text" placeholder="手机号/邮箱" v-model="account"/>
            </div>
            <div class="pwd">
                            密码
                <span></span>
                <input type="password" placeholder="请输入您的密码"  v-model="pwd"/>
            </div>
            <div class="loginbtn">
                <button @click="submitval">立即登录</button>
                <router-link to="zhuce">去注册>></router-link>
            </div>
        </div>
    </div>
</template>

<script>
    
    export default {
        name: 'login',
        
        data() {
            return {
                msg: '打开vue的大门',
                account:"",
                pwd:""
            }
        },
        methods: {
            submitval:function(){
                var account = this.account;
                var pwd = this.pwd;
                if(account == '123' && pwd == 'admin'){
                    this.$store.commit("setAccount", account);
                    this.$router.push({
                        path: "/main"
                    });
                    
                    
                }
            }
        }
    }
</script>

<style scoped>
    .loginwrap{
        width: 100%;
        height: 100%;
        background-color: #2C3E50;

    }
    .login {
        background: rgba(0, 0, 0, 0.3);
        width: 424px;
        height: 424px;
        font-family: "微软雅黑";
        margin: 0 auto;
        border-radius: 5px;
        background-color: #41B883;
        padding: 20px 60px;
       position: absolute;
       top: 300px;
       left: 36%;
        
    }
    
    h1 {
        color: #fff;
        margin-bottom: 30px;
        margin-top: 40px;
    }
    
    .account span,
    .pwd span {
        display: inline-block;
        vertical-align: top;
    }
    
    .account input,
    .pwd input {
        outline: none;
        border: 0;
        width: 300px;
        height: 40px;
        font-size: 20px;
    }
    
    .account span {
        width: 20px;
        height: 21px;
        background: url(../assets/dialog-account.png) 50% no-repeat;
        background-color: #fff;
        display: inline-block;
        vertical-align: top;
        height: 43px;
        
    }
    .account{
        margin-bottom: 20px;
        background-color: #fff;
        color: #41B883;
        font-size: 20px;
        font-weight: 400;
        border-radius: 5px;
    }
    .pwd{
        background-color: #fff;
        margin-bottom: 30px;
        color: #41B883;
        font-size: 20px;
        font-weight: 400;
        border-radius: 5px;
    }
    .pwd span {
        width: 20px;
        height: 16px;
        background: url(../assets/dialog-pwd.png) 50% no-repeat;
        
        display: inline-block;
        vertical-align: top;
        height: 43px;
    }
    .loginbtn button{
        outline: none;
        border: 0;
        background-color: #35495E;
        width: 100%;
        height: 43px;
        font-size: 26px;
        color: #fff;
        border-radius: 5px;
    }
   
    .router-link-active {
        text-decoration: none;
    }
</style>


